import React, { Component } from 'react';
import swal from 'sweetalert';
import axios from 'axios';
import Select from './Select';
import ToggleButton from './ToggleButton';
import CardContainer from './CardContainer';
import { postMessage } from "../Utils/middleware";
import { serverUrl } from '../Constants/messages';
import str from '../Constants/localization'
import { animateScroll } from 'react-scroll';

class MessageInput extends Component {

	state = {
		value: '',
		isFocused: false
	};
	handleTouchStart = false;

	getStaticMessage() {
		const { addMessage, message } = this.props;
		if (Array.isArray(message.message)) {
			return (
				<div className='message-array'>
					{message.message.map((message, i) => {
						return (<div
							className='message message-static'
							onTouchStart={() => {
								addMessage({
									type: 'user',
									...message
								});
							}}
							key={i}
						>
							{message.message}
						</div>);
					})}
				</div>
			);
		}
		return (
			<div className='message message-static' onTouchStart={() => { addMessage(message); }}>
				{message.message}
			</div>);
	}

	getInputMessage() {
		const { value, isFocused } = this.state;
		const { message, isIphoneX, isAndroid } = this.props;
		return (
			<div className='message-input-outer'>
				<div className='message-input-container'>
					<input
						type='text'
						value={value}
						placeholder='Type here'
						onChange={this.onChange}
						onKeyPress={(e) => {
							if (e.charCode === 13) this.addMessage();
						}}
						onFocus={() => {
							this.setState({
								isFocused: true
							},() => {
								animateScroll.scrollToBottom({
									duration: 500,
									smooth: "easeInOutQuad",
								})
							});
						}}
						onBlur={() => {
							this.setState({
								isFocused: false
							});
						}}
					/>
					<div className={`send-button ${value ? '' : 'disabled'}`} onTouchEnd={e => {
						if (this.handleTouchStart) {
							setTimeout(() => {
								this.handleTouchStart = false;
							}, 1000);
							e.preventDefault();
							return;
						}
						this.handleTouchStart = true;
						this.addMessage();
					}}>Go</div>
				</div>
				{
					isFocused && !isAndroid &&
					<div
						style={{
							height: isIphoneX ? 350 : 300,
							backgroundColor: '#FF2554',
							marginLeft: -20,
							marginRight: -20
						}}
					/>
				}
			</div>);
	}

	// {message.key == 'email' ? <div className={`hidden-email-text`} onTouchEnd={e => {
	// 	if (this.handleTouchStart) {
	// 		setTimeout(() => {
	// 			this.handleTouchStart = false;
	// 		}, 1000);
	// 		e.preventDefault();
	// 		return;
	// 	}
	// 	this.handleTouchStart = true;
	// 	this.addMessage();
	// }}>I don't want email alerts</div> : <span></span>}


	getSelectInput() {
		const { addMessage, message } = this.props;
		const { options } = message;
		return <Select options={options} addMessage={addMessage} />;
	}

	setSelectedOption = (index) => {
		const { addMessage, message } = this.props;
		const selectedOption = message.options[index]['value'];
		postMessage(message.key, selectedOption);
		addMessage({
			type: 'user',
			message: selectedOption,
			inputType: 'toggleInput'
		});
	}

	getToggleButton() {
		const { options } = this.props.message;
		return <ToggleButton options={options} setSelectedOption={this.setSelectedOption} />
	}


	getCardInput() {
		const { addMessage, message } = this.props;
		const { cards } = message;
		return <CardContainer
			addMessage={addMessage} cards={cards} message={message} />
	}

	onChange = (e) => {
		this.setState({
			value: e.target.value
		})
	};

	addMessage = () => {
		const { addMessage, message } = this.props;
		const { value } = this.state;
		if (message.key === "email") {
			if (value && !(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value)))
				swal("Invalid Email", "Please enter valid email address!", "error", {
					confirm: {
						className: "custom-button",
					}
				});
			else if(value) {
				// axios.post(serverUrl + 'users/alreadyregistered', { email: value }).then((res) => {
				// 	if (res.data.isRegistered) {
				// 		swal("User with this email is already registered", "", "error", {
				// 			confirm: {
				// 				className: "custom-button",
				// 			}
				// 		});
				// 	} else {
				// 		postMessage(message.key, value);
				// 		addMessage({
				// 			type: 'user',
				// 			message: value,
				// 			inputType: 'input',
				// 			isNext: message.isNext
				// 		});
				// 	}
				// }).catch((e) => {
				// 	swal("User with this email is already registered", "", "error", {
				// 		confirm: {
				// 			className: "custom-button",
				// 		}
				// 	});
				// })
				postMessage(message.key, value);
				addMessage({
					type: 'user',
					message: value,
					inputType: 'input',
					isNext: message.isNext
				});
			} else {
				postMessage(message.key, null);
				addMessage({
					type: 'user',
					message: 'not now',
					inputType: 'input',
					isNext: message.isNext
				});
			}
		}
		else if (message.key !== "email") {
			postMessage(message.key, value);
			addMessage({
				type: 'user',
				message: value,
				inputType: 'input',
				isNext: message.isNext
			});
		}
	};

	componentWillLeave(callback) {
		const { message } = this.props;
		if (message.inputType !== 'input') {
			this.setState({
				leaving: true,
			});
			setTimeout(() => {
				callback();
			}, 400);

		} else callback();
	}

	render() {
		const { leaving } = this.state;
		const { message } = this.props;
		return message ? (
			<div className={`message-input-wrapper ${message.inputType} ${leaving ? 'leaving' : ''}`}>
				{message.inputType === 'static' ? this.getStaticMessage() : null}
				{message.inputType === 'input' ? this.getInputMessage() : null}
				{message.inputType === 'select' ? this.getSelectInput() : null}
				{message.inputType === 'card' ? this.getCardInput() : null}
				{message.inputType === 'toggleButton' ? this.getToggleButton() : null}
			</div>
		) : null;
	}
}

export default MessageInput;



