import React, { Component } from 'react';
import { TransitionGroup } from 'react-transition-group';
import { animateScroll } from  'react-scroll';
import MessageItem from './MessageItem';
import MessageInput from './MessageInput';
import {
	getBotMessageGroup,
	getBetweenTimeoutValue,
	getTimeoutValue,
	getInputTimeoutValue
} from '../Utils/botMessages';
import { getUserMessage } from '../Utils/userMessages';
import { postMessage } from "../Utils/middleware";

class MessageList extends Component {
	state = {
		showInput: false,
		messages: [],
		name: '',
		isIphoneX: true,
		isAndroid: false
	};

  constructor(props) {
    super(props);
    this.imageUrl = require('../images/Past_Deals.png');
    this.image = <img src={this.imageUrl} alt=""/>;
    this.messages = [
      {
        type: 'user',
        message: 'Hello'
      },
    ];
  }

  componentDidMount() {
		this.getBotMessageGroup();
		document.addEventListener("message", (data) => {
			const {notificationAllowed, isIphoneX, isAndroid} = JSON.parse(data.data);
			if (isIphoneX !== undefined) {
				this.setState({
					isIphoneX
				});
			}

			if (isAndroid !== undefined) {
				this.setState({
					isAndroid
				});
			}

			if (notificationAllowed !== undefined) {
				if (notificationAllowed === false) {
					this.stoppedMessage.message = "Don't Allow"
				}

				this.setMessageInState(this.stoppedMessage);
				this.toggleUserInput();
				if (notificationAllowed) {
					for(let i = 0; i < this.stoppedMessage.moveBot; i++) {
						getBotMessageGroup();
					}
					for(let i = 0; i < this.stoppedMessage.moveUser; i++) {
						getUserMessage();
					}
				}
				this.getBotMessageGroup();
			}
		});
  }

  getBotMessageGroup = () => {
		this.setState({
			showInput: false
		});
		const messageGroup = getBotMessageGroup();

		if (messageGroup) {
			messageGroup.forEach((message, index) => {
				const timeoutValue = getTimeoutValue() + (index ? index*getBetweenTimeoutValue() : 0);
				setTimeout(() => {
					this.setMessageInState(message);
					this.scrollToBottom();
					if (index === messageGroup.length - 1) this.toggleUserInput(true);
				}, timeoutValue);
			});
		}
	};

  scrollToBottom(delay = 0) {
		if (delay) {
			setTimeout(() => {
				animateScroll.scrollToBottom({
					duration: 500,
					smooth: "easeInOutQuad",
				})
			}, delay)
		} else {
			animateScroll.scrollToBottom({
				duration: 500,
				smooth: "easeInOutQuad",
			})
		}

	}

  setMessages() {
    const { messages } = this.state;
    return messages.map((message, i) => {
      const firstChild = i !== 0 && messages[i-1].type !== message.type;
      if (message.type === 'card-static') {
				return <div className='card-static-wrapper' key={i}>
					{this.image}
				</div>
			}
      return <MessageItem
        message={message}
        firstChild={firstChild}
        key={i}
        timeoutValue={getTimeoutValue(message.message)}
      />
    })
  }

  toggleUserInput(timeout) {
		if (!this.state.showInput) this.scrollToBottom(500);
		const { userMessage } = this.state;
		const showInput = !this.state.showInput;
		if (timeout) {
			setTimeout(() => {
				if (!this.state.showInput) this.scrollToBottom(500);
				this.setState({
					showInput,
					userMessage: showInput ? getUserMessage() : userMessage
				});
			}, getInputTimeoutValue());
		} else {
			this.setState({
				showInput,
				userMessage: showInput ? getUserMessage() : userMessage
			});
		}
  }

  setMessageInState(message) {
    const messages = this.state.messages.slice();
    messages.push(message);
    this.setState({messages});
  }

	addMessage = (message) => {
		console.log(message);

		if (message.finish) postMessage('onboardingFinished', true);

		if (message.inputType === 'input' && !message.isNext) {
			setTimeout(() => {
				this.scrollToBottom();
				this.setMessageInState({
					type: 'bot',
					message: `Hi ${message.message}! Which cities would you like to receive deals departing from? (Select up to 3 cities)`
				});
				this.toggleUserInput(true);
			}, getBetweenTimeoutValue());

		} else {
			if (typeof message.allowNotification !== 'undefined') {
				if (this.stoppedMessage) {
					postMessage('allowNotificationsTwice', message.allowNotification);
				} else {
					postMessage('allowNotifications', message.allowNotification);
				}
			}
			if (typeof message.moveBot !== 'undefined' && message.moveBot) {
				if (message.allowNotification === true) {
					if (message.twiceAsked) {
						for(let i = 0; i < message.moveBot; i++) {
							getBotMessageGroup();
						}
					} else {
						this.stoppedMessage = message;
						return;
					}
				} else {
					for(let i = 0; i < message.moveBot; i++) {
						getBotMessageGroup();
					}
				}
			}
			if (typeof message.moveUser !== 'undefined' && message.moveUser && message.allowNotification !== true) {
				for(let i = 0; i < message.moveUser; i++) {
					getUserMessage();
				}
			}
			if (!message.finish) {
				setTimeout(() => {
					this.getBotMessageGroup();
				}, 1000);
			}
		}

		this.setMessageInState(message);
		this.toggleUserInput();
	};

  render() {
  	const { showInput, userMessage, isIphoneX, isAndroid } = this.state;
    return (
      <div className="message-list-wrapper">
				<div className='preload-image'>{this.image}</div>
        {this.setMessages()}
				<TransitionGroup component='div' className='message-input-container' >
					{
						showInput ?
						<MessageInput
							message={userMessage}
							addMessage={this.addMessage}
							isIphoneX={isIphoneX}
							isAndroid={isAndroid}
						/>
						: null}
				</TransitionGroup>
      </div>
    );
  }
}

export default MessageList;



